import React, { Component } from 'react';
import reactLogo from'./React-logo.png';
import jsLogo from'./JavaScript-logo.png';
import bootstrapLogo from'./Bootstrap-logo.png';
import NavBar from "../navBar";

class About extends Component {
	render() {
    return (
      <div className="About">
      	<NavBar />
		<div className="card mt-3 ml-5 mr-5">
		  <div className="card-body">
		    <h5 className="card-title">Acerca de</h5>
		    <p className="card-text">Herramienta didactica para calculo de enlace. Pensada por estudiantes para estudiantes.</p>

		     <h6 className="card-title">Stack utilizado</h6>
		     <img alt="" className="mr-5" src={reactLogo} width="100px;" />
		     <img alt="" className="mr-5" src={jsLogo} width="100px;" />
		     <img alt="" className="mr-5" src={bootstrapLogo} width="100px;" />
		  </div>
		</div>
      </div>
    );
  }
}

export default About;